

.box {
    margin: 25px 0;
    width: 600px;
    height: 90px;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.box .title {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    height: inherit;
}

.box .title .block {
    width: 0%;
    height: inherit;
    background: #e84545;
    position: absolute;
    animation: mainBlock 2s cubic-bezier(.74, .06, .4, .92) forwards;
    display: flex;
  }

  .box .title h2 {
    font-family: 'Poppins';
    color: #fff;
    font-size: 90px;
    -webkit-animation: mainFadeIn 2s forwards;
    -o-animation: mainFadeIn 2s forwards;
    animation: mainFadeIn 2s forwards;
    animation-delay: 1.6s;
    opacity: 0;
    display: flex;
    align-items: baseline;
    position: relative;

  }

  .box .title h2 span {
    width:0px;
    height: 0px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;

    background: #e84545;
    -webkit-animation: load 0.6s cubic-bezier(.74, .06, .4, .92) forwards;
    animation: popIn 0.8s cubic-bezier(.74, .06, .4, .92) forwards;
    animation-delay: 2s;
    margin-left: 5px;
    margin-top: -10px;
    position: absolute;
    bottom: 33px;
    right: -30px;
  }

  @keyframes mainBlock {
    0% {
      width: 0%;
      left: 0;
  
    }
    50% {
      width: 100%;
      left: 0;
  
    }
    100% {
      width: 0;
      left: 100%;
    }
  }

  
@keyframes mainFadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  

  @keyframes popIn {
    0% {
      width: 0px;
      height: 0px;

      background: #e84545;
      border: 0px solid #ddd;
      opacity: 0;
    }
    50% {
      width: 30px;
      height: 30px;

      background: #e84545;
      opacity: 1;
      bottom: 65px;
    }
     65% {
        width: 21px;
      height: 21px;
        bottom: 20px;
     }
     80% {
        width: 30px;
      height: 30px;
        bottom: 40px
     }
    100% {
      width: 21px;
      height: 21px;
      background: #e84545;
      border: 0px solid #222;
      bottom: 33px;
  
    }
  }

  @media screen and (max-width: 600px) {
    .box {
        width: 325px;
        height: 50px;
    }     

    .box .title h2{
        font-size: 50px;
    }

    .box .title h2 span {
        bottom: 20px;
        right: -20px;
      }
    

    @keyframes popIn {
        50% {
          width: 15px;
          height: 15px;
          bottom: 45px;
        }
         65% {
            width: 11px;
          height: 11px;
            bottom: 10px;
         }
         80% {
            width: 15px;
          height: 15px;
            bottom: 30px
         }
        100% {
          width: 11px;
          height: 11px;
          bottom: 20px;
        }
      }
  }